/* Write your CSS here */
.tab-item {
  list-style-type: none;
}

.tab-button {
  color: #7b8794;
  font-family: 'Roboto';
  font-size: 14px;
  font-weight: 800;
  border: none;
  margin: 15px;
  outline: none;
  cursor: pointer;
  background-color: transparent;
}
.active {
  color: #2563eb;
  border-bottom: 2px solid #2563eb;
  padding-bottom: 8px;
}

@media screen and (min-width: 768px) {
  .tab-button {
    font-size: 16px;
  }
  .active {
    padding-bottom: 12px;
  }
}
